/*--------------------------------------------------------------------
    Status Matrix
    _status-matrix.css
    Status matrix on the homepage
--------------------------------------------------------------------*/
.status-matrix svg {
  vertical-align: middle;
}
.status-matrix__wrapper {
  margin-bottom: 4em;
}
.status-matrix {
  padding: 1em 0 1.5em 0;
}
.status-matrix tr:last-child {
  border-bottom: none;
}
.status-matrix table {
  margin-bottom: 0;
  table-layout: fixed;
}
.status-matrix a {
  display: block;
}
.status-matrix tr {

}
.status-matrix th {
  text-align: center;
  padding: 1em 0 1em .375em;
}
.status-matrix--no-group-status th {
  text-align: left;
  text-indent: -2em;
}
.status-matrix td {
  border-right: 1px solid #ddd;
  overflow: hidden;
  padding-top: .75em;
  padding-bottom: .75em;
}
.status-matrix td:last-child {
  border-right: none;
}

.status-matrix__campus {
  font-weight: 700;
  font-size: .825em;
  line-height: 3;
  text-align: center;
  -webkit-transform: rotate(-90deg);
  -moz-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  -o-transform: rotate(-90deg);
  /* Internet Explorer */
  filter: progid:DXImageTransform.Microsoft.BasicImage(rotation=3);
}

.status-matrix__campus abbr {
  border-bottom: 1px dotted;
}

.status-matrix__category {
  position: relative;
}


/*
    Absolutely positioned method icons. Giving category and
    service rss icons different classes for now just incase
    we need to style differently.
*/
.status-matrix__category-rss,
.status-matrix__service-rss {
  position: absolute;
  top: .55em;
  right: 1.5em;
  width: 23px;
  height: 23px;
  text-align: center;
  border-radius: 999px;
  overflow: hidden;
  background-color: #eee;
  color: #666;
  transition: all .3s ease;
  /*
      TO-DO: This may not be the best solution for creating these
      round RSS icons. We may need to revisit. Using a little bit of
      top and left padding to center the icon.
  */
}
.status-matrix__category-rss svg,
.status-matrix__service-rss svg {
  color: #333;
  font-size: .825em;
}
.status-matrix__category-rss:hover,
.status-matrix__service-rss:hover {
  background-color: #f25b19;
}
.status-matrix__category-rss:hover svg,
.status-matrix__service-rss:hover svg {
  color: #fff;
}

.status-matrix__service {
  position: relative;
  padding-left: 2em;
}

.status-matrix__category,
.status-matrix__service {
  text-align: left;
}

.status-matrix__reporting-off-icon {
  position: absolute;
  left: 6px;
  color: #bbb;
}

.status-matrix__service a:hover {
  text-decoration: underline;
  color: #990000;
}

.status-matrix__service-title {
  display: inline-block;
  max-width: 70%;
}

.status-matrix__info-indicator {
  color: #49afc7;
}

.status-matrix__sub-row {
  background-color: #fff;
}
.is-expand-all .status-matrix__sub-row {
  display: table-row;
}

.status-icon {
  text-align: center;
}

.status-icon--good .fa {
  color: #22b573;
}

.status-icon--ongoing .fa {
  color: #f5bb17;
}

.status-icon--alert .fa {
  color: #f25b19;
}

.status-icon--maintenance .fa {
  color: #a7a9ab;
}

.status-icon--info .fa {
  color: #49afc7;
}

.status-icon--grey .fa {
  color: inherit;
}

/*
    This handles the transition for the header row as it
    changes to the "subtle" state
*/
.status-matrix__header-row {
  transition: all .3s ease;
  background-color: #f7f7f7;
}


/*
    Subtle js state class for expanded service category rows.
    for example
    Authentication (Main category row)
        CAS
        OTP
        Keberos
*/
.is-subtle {
  background-color: #eee;
}
.is-subtle .status-icon i {
  color: #ccc !important;
}
.is-subtle .status-matrix__category-toggle,
.is-subtle .status-matrix__category-toggle:hover {
  color: #ccc;
}
.is-subtle .status-matrix__category-toggle:hover i {
  color: #ccc;
}
.is-subtle .status-matrix__category-rss,
.is-subtle .status-matrix__service-rss {
  background-color: #eee;
  cursor: default;
}
.is-subtle .status-matrix__category-rss i,
.is-subtle .status-matrix__service-rss i {
  color: #ccc;
}

.status-matrix__category-toggle {
  width: 280px;
  text-align: left;
  color: #333;
  padding-left: 2em;
  text-indent: -1.5em;
}
.status-matrix__category-toggle:hover {
  color: #990000;
}
.status-matrix__category-toggle:hover i {
  color: #990000;
}
.status-matrix__category-toggle i {
  color: #ccc;
}

@media screen and (max-width: 48em) {
  .status-matrix {
    padding: 1em 0;
    margin: 0;
    background-color: #fff;
    border: none;
  }

  .status-matrix__wrapper {
    background-color: #fff;
    border: none;
  }

  .status-matrix__category-toggle {
    width: auto;
    width: initial;
  }
}


/* status-matrix--single displays affected campuses on single notice pages */
.status-matrix--single {
  padding: 0;
  border: 1px solid #eee;
  background: none;
}
.status-matrix--single .status-matrix__campus {
  -webkit-transform: none;
  -moz-transform: none;
  -ms-transform: none;
  -o-transform: none;
}
.status-matrix--single th {
  border: 1px solid #ddd;
  border-top: none;
  background: #fafafa;
}
.status-matrix--single th:first-child {
  border-left: none;
}
.status-matrix--single th:last-child {
  border-right: none;
}

/*------------------------------------------------------------------------------
    Status key
    Key/legend for icons and color
------------------------------------------------------------------------------*/
.status-key {
  margin-bottom: 3em;
  background-color: transparent;
}

.status-key__list {
  list-style: none;
  padding: 0;
  box-sizing: border-box;
  margin: 0;
  text-align: center;
}
.status-key--left .status-key__list {
  text-align: left;
}
.status-key__right .status-key__list {
  text-align: right;
}

.status-key__item {
  display: inline-block;
  color: #666;
  margin-right: 1em;
}
.status-key__item i {
  display: inline-block;
  font-size: 1.2em;
}
.status-key__item span {
  font-size: .875em;
}

.status-key__item--alert i {
  color: #f25b19;
}

.status-key__item--ongoing i {
  color: #f5bb17;
}

.status-key__item--info i {
  color: #49afc7;
}

.status-key__item--good i {
  color: #22b573;
}

.status-key__item--maintenance i {
  color: #a7a9ab;
}

/* Modifies .status-key so that it's more like a regular vertical list */
.status-key--secondary .status-key__list {
  list-type: none;
  padding: 0;
  margin: 0;
  text-align: left;
}
.status-key--secondary .status-key__item {
  color: #666;
  display: block;
  padding: .5em 0;
}
.status-key--secondary .status-key__item i {
  display: inline-block;
  font-size: 1.2em;
}
.status-key--secondary .status-key__item span {
  font-size: .875em;
}

@media screen and (max-width: 48em) {
  .status-key {
    margin-bottom: 0;
  }

  .status-key__item {
    display: block;
    text-align: center;
    padding: .5em 0;
  }
}

.rvt-dropdown__menu svg {
  vertical-align: middle;
  margin-bottom: 3px;
  color: #006298;
}

.rvt-dropdown__menu a:hover svg {
  color: #fff;
}
/*--------------------------------------------------------------------
    Message box
    _message-box.css
    generic message boxes for holding lists or messages
--------------------------------------------------------------------*/

.message-box {
  border: 1px solid #ccc;
  border-radius: 2px;
  margin: 1em 0;
  position: relative;
  z-index: 1;
  background: #fff;
}

.message-box-header {
  background-color: #f7f7f7;
  border-bottom: 1px solid #ccc;
  padding: 1.2em 1.65em 1.2em 1em;
}
.message-box-header:after {
  /* clearfix */
  content: "";
  display: table;
  clear: both;
}

.message-box-header__message,
.message-box-header__info {
  float: left;
  font-size: 1.125em;
  margin: 0;
}
.message-box-header__info {
  float: right;
}

.message-box-header__message--subtle,
.message-box-header__info--subtle {
  color: #999;
}

.message-box--secondary .message-box-header__message {
  font-weight: normal;
}

.message-box-header__count {
  position: absolute;
  right: 2em;
  top: 1em;
  float: right;
  font-size: .875em;
  font-weight: bold;
  color: #fff;
  z-index: 1;
}
.message-box-header__count:before {
  content: "";
  width: 2em;
  height: 2em;
  margin-left: -1em;
  margin-top: -1em;
  position: absolute;
  left: 50%;
  top: 50%;
  background-color: #999;
  border-radius: 50%;
  z-index: -1;
}
.message-box-header__icon {
  color: #999;
}

.message-box__body {
  padding: .5em 1em;
  font-size: .876rem;
}

.message-box__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.message-box__list-item {
  padding: .75em 1em;
  border-bottom: 1px solid #ccc;
}
.message-box__list-item:last-child {
  border-bottom: none;
}
.message-box__list-item a {
  color: inherit;
  display: block;
}
.message-box__list-item a:hover {
  text-decoration: underline;
  color: #333;
}

/* action buttons */
.message-box__actions {
  text-align: right;
}
.message-box__actions-button {
  height: 2em;
  width: 2em;
  margin-left: .25em;
  border: 1px solid #ccc;
  border-radius: 50%;
  background: transparent;
}
.message-box__actions-button:hover {
  background: #900;
  color: #fff;
}
.message-box__actions-button.is-active {
  background: #900;
  border-color: #fff;
  color: #fff;
}
.message-box__actions-button.is-disabled {
  background: #ccc;
  color: #aaa;
  border-color: #aaa;
}

.message-box__hidden-actions {
  position: absolute;
  right: 5px;
  bottom: 0;
  height: 0;
  transition: all .1s;
}
.message-box:hover .message-box__hidden-actions {
  bottom: 5px;
  height: 2em;
}
.message-box__hidden-actions-button {
  border: 1px solid #ccc;
  border-radius: 50%;
  background: transparent;
}

/* minor version has smaller header, less padding */
.message-box--minor .message-box-header {
  padding: .8em 1em;
}
.message-box--minor .message-box-header__message,
.message-box--minor .message-box-header__info {
  font-size: .875em;
}

/* Good modifier */
.message-box--good .message-box-header {
  background-color: #ddF4ea;
}

/* Info modifier */
.message-box--info .message-box-header {
  background-color: #eef9fd;

}
.message-box--info .message-box-header__icon {
  color: #49afc7;
}
.message-box--info .message-box__list-item-date {
  color: #49afc7;
}

/* Ongoing issue modifier */
.message-box--ongoing .message-box-header {
  background-color: #fef9ed;
  border-bottom: 1px solid #e8d3a6;
}
.message-box--ongoing .message-box-header__icon {
  color: #f5bb17;
}
.message-box--ongoing .message-box-header__count:before {
  background-color: #f5bb17;
}

/* Alert modifier */
.message-box--alert .message-box-header {
  background-color: #fef2ec;
  border-bottom: 1px solid #efd5cb;
}
.message-box--alert .message-box-header__icon {
  color: #f25b19;
}
.message-box--alert .message-box-header__count:before {
  background-color: #f25b19;
}


/* accordion version of message box turns head in to trigger and body in to expandable target */
.message-box--accordion {
  border-radius: 99em;
}
.message-box--accordion.is-open {
  border-radius: 2px;
  z-index: 2;
}
.message-box--accordion .message-box-header:hover {
  cursor: pointer;
}
.message-box--accordion .message-box__list,
.message-box--accordion .message-box__body {
  display: none;
}
.message-box--accordion.is-open .message-box__list,
.message-box--accordion.is-open .message-box__body {
  display: block;
}


/* a whee little one */
.message-box--micro {
  border: 1px solid #eee;
}
.message-box--micro .message-box-header {
  padding: .4em 1em;
  background: none;
  border-bottom: none;
}
.message-box--micro .message-box-header__message,
.message-box--micro .message-box-header__info {
  font-size: .875rem;
  color: #aaa
}

.message-box--micro .message-box__body {
  font-size: .875rem;
  font-style: italic;
}
.message-box--micro .message-box__body *:last-child {
  margin-bottom: 0;
}
